.main-hero {
  position: relative;
}
.main-hero::before {
  content: "";
  position: absolute;
  width: 100%;
  min-height: 302px;
  background-color: rgba(104, 183, 56, 0.85);
  z-index: -1;
}
@media (min-width: 768px) {
  .main-hero::before {
    display: none;
  }
}
.main-hero::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 302px;
  background-image: url("../img/index-background-mobile@1x.jpg");
  background-position: top center;
  background-repeat: no-repeat;
  background-size: inherit;
  z-index: -2;
}
@media (min-resolution: 2dppx) {
  .main-hero::after {
    background-image: url("../img/index-background-mobile@2x.jpg");
    background-size: contain;
  }
}
@media (min-width: 768px) {
  .main-hero::after {
    display: none;
  }
}

